import React from 'react'

const Front = (props) => (

    <div className="front__card">
        <img className="front__img" src={`${props.info.thumbNail}`} alt=""/>

        <div className="front-content">
        <p className="text__sm text__light">{props.info.date}</p>
        <h3 className="text__large">{props.info.title}</h3>
        <div className="front__card__group">
            {props.info.tag.map((item, index) => (
                    <p className="text__sm text__light" key={index}>{item}</p>
            ))}
        </div>
        </div>
     

    </div>

)

export default Front;